html{font-family:"Montserrat",sans-serif;font-size:16px}a{color:#A60F2D}a:active{color:#CA1237}a:hover{color:#CA1237}a:visited{color:#A60F2D}h1,h2,h3,h4,h5,h6{-webkit-transition:font-size 300ms cubic-bezier(0, 0, 0.03, 1);-o-transition:font-size 300ms cubic-bezier(0, 0, 0.03, 1);transition:font-size 300ms cubic-bezier(0, 0, 0.03, 1)}p{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.wsu-u-visually-hidden{position:absolute;overflow:hidden;clip:rect(1px, 1px, 1px, 1px);width:1px;height:1px;margin:-1px;padding:0;-webkit-clip-path:inset(50%);clip-path:inset(50%)}.wsu-u-spacer{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;height:200px}.wsu-u-title{font-family:monospace;text-align:center}.wsu-c-button a,.wsu-c-button a:visited a{display:inline-table;margin:0;padding:15px 30px;-webkit-transition:background-color 300ms cubic-bezier(0, 0, 0.03, 1);-o-transition:background-color 300ms cubic-bezier(0, 0, 0.03, 1);transition:background-color 300ms cubic-bezier(0, 0, 0.03, 1);text-decoration:none;color:#fff;border:none;border-radius:3px;background-color:#CA1237;font-size:12px;font-size:.75rem;font-weight:600;line-height:1.5}.wsu-c-button a:hover,.wsu-c-button a:active{cursor:pointer;color:#fff;background-color:#A60F2D}
